import axios from 'axios';

// Configuracin de la API optimizada para mviles
export const API_CONFIG = {
  // URL base del servidor de produccin
  BASE_URL: process.env.REACT_APP_API_URL || 'https://derwill-deposito-backend.onrender.com',
  
  // Sin timeout para conexiones lentas
  TIMEOUT: 0,
  
  // Headers optimizados (sin headers problemticos de CORS)
  DEFAULT_HEADERS: {
    'Content-Type': 'application/json',
  },
  
  // Configuracin de reintentos
  RETRY_CONFIG: {
    retries: 2,
    retryDelay: 1000,
    retryCondition: (error) => {
      return error.code === 'ECONNABORTED' || 
             error.response?.status >= 500 || 
             error.message.includes('timeout');
    }
  },
  
  // Configuracin de cach
  CACHE_CONFIG: {
    // Tiempo de cach en milisegundos (5 minutos)
    defaultTTL: 5 * 60 * 1000,
    // Tamao mximo del cach (50 MB)
    maxSize: 50 * 1024 * 1024,
  }
};

// Log para debugging
console.log(' API_CONFIG:', {
  NODE_ENV: process.env.NODE_ENV,
  REACT_APP_API_URL: process.env.REACT_APP_API_URL,
  BASE_URL: API_CONFIG.BASE_URL
});

// Configuracin de axios optimizada
export const axiosConfig = {
  baseURL: API_CONFIG.BASE_URL,
  timeout: API_CONFIG.TIMEOUT,
  headers: API_CONFIG.DEFAULT_HEADERS,
  // Configuracin para mejorar rendimiento en mviles
  maxContentLength: 10 * 1024 * 1024, // 10MB mximo
  maxBodyLength: 10 * 1024 * 1024, // 10MB mximo
};

// Cliente axios simple
export const apiClient = axios.create({
  baseURL: API_CONFIG.BASE_URL,
  timeout: API_CONFIG.TIMEOUT,
  headers: API_CONFIG.DEFAULT_HEADERS,
  maxContentLength: 10 * 1024 * 1024,
  maxBodyLength: 10 * 1024 * 1024,
}); 